body{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 13px;
    font-weight: 400;
    color: #8184a1;
    line-height: 1.3;
}

h4{
    margin-top: 0;
    margin-bottom: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.container{
    display: flex;
    width: 100%;
    height: 90vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.quantity{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
}

.quantity__minus,
.quantity__plus{
    display: block;
    width: 32px;
    height: 32px;
    margin: 0;
    background: #dee0ee;
    text-decoration: none;
    text-align: center;
    line-height: 33px;
    font-size:20px;
}

.quantity__minus:hover,
.quantity__plus:hover{
    background: #575b71;
    color: #fff;
}

.quantity__minus{
    border-radius:3px 0 0 3px;
}

.quantity__plus{
    border-radius:0 3px 3px 0;
}

.quantity__input{
    width: 30px;
    height: 29px;
    margin: 0;
    padding: 0;
    text-align: center;
    border-top: 2px solid #dee0ee;
    border-bottom: 1px solid #dee0ee;
    border-left: 1px solid #dee0ee;
    border-right: 2px solid #dee0ee;
    background:#fff;
    color:#818481;
}

.quantity__minus:link,
.quantity__plus:link{
    color:#8184a1
}

.quantity__minus:visited,
.quantity__plus:visited{
    color:#fff
}